@media screen and (min-width: 575px) {
  .kit-card {
    min-height: 138px; } }

.kit-card .icon {
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s cubic-bezier(0.25, 0.1, 0.25, 1);
  color: #0056CF; }
  .kit-card .icon .mobile {
    display: none; }
  @media screen and (max-width: 575px) {
    .kit-card .icon.mobile {
      display: block;
      visibility: visible;
      opacity: 1; }
    .kit-card .icon.desktop {
      display: none; } }

.kit-card-header {
  height: 40px; }

.kit-card-description {
  font-size: 14px !important; }
  @media screen and (max-width: 575px) {
    .kit-card-description {
      display: none; } }

.kit-card:hover {
  box-shadow: 0 12px 28px 0 rgba(60, 106, 172, 0.18);
  cursor: pointer; }
  @media screen and (max-width: 575px) {
    .kit-card:hover {
      box-shadow: none; } }
  @media screen and (min-width: 575px) {
    .kit-card:hover .icon.desktop {
      visibility: visible;
      opacity: 1; } }
